import {Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography} from "@mui/material";
import {useCallback, useEffect, useState} from "react";

function ReplaceFileDialog({ onClose, fileList, onReplace }) {
    const [queue,setQueue] = useState([]);
    const [current,setCurrent] = useState({});
    useEffect(()=>{
        const newq = fileList.map(f=>f);
        setQueue(newq);
        setCurrent(newq[0]);
    },[fileList])
    const handleClose = () => {
        onClose();
    };

    const handleReplace = useCallback(() => {
        onReplace(current);
        queue.shift();
        if (!queue.length)
            onClose();
        else {
            setQueue(queue);
            setCurrent(queue[0]);
        }
    },[current,queue]);
    const handleReplaceAll = useCallback(() => {
        queue.forEach(f=> onReplace(f));
        onClose();
    },[queue]);
    const handleSkip = useCallback(() => {
        queue.shift();
        if (!queue.length)
            onClose();
        else {
            setQueue(queue);
            setCurrent(queue[0]);
        }
    },[current,queue]);

    return (
        <Dialog onClose={handleClose} open={true}>
            <DialogTitle>Replace or Skip {fileList.length} file{fileList.length>1?'s':''}</DialogTitle>
            <DialogContent>
                <Typography>Already has file named "{current.name}"</Typography>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleReplace}>Replace</Button>
                <Button onClick={handleSkip}>Skip</Button>
                <Button onClick={handleReplaceAll} autoFocus>
                    Replace All
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default ReplaceFileDialog