import AutoSizer from 'lp-react-virtualized-auto-sizer-react-18'
import {
    Chip,
    Divider,
    IconButton,
    List,
    ListItem,
    ListItemText,
    Toolbar,
    Typography,
    Paper,
} from '@mui/material'
import { FilterList } from '@mui/icons-material'
import FilterDrawer from 'material-ui-shell/lib/components/FilterDrawer'
import Page from 'material-ui-shell/lib/containers/Page'
import React, {useEffect, useCallback, useState, useRef, useTransition} from 'react'
import SearchField from 'material-ui-shell/lib/components/SearchField'
import { ViewportList } from 'react-viewport-list';
import { Scrollbars } from 'react-custom-scrollbars-2'
import { useFilter } from 'material-ui-shell/lib/providers/Filter'
import { useIntl } from 'react-intl'
import { useTheme as useAppTheme } from 'material-ui-shell/lib/providers/Theme'
import {getApp} from "../../containers/Database/app";
import Grid from "@mui/material/Unstable_Grid2";
import Map from "../../components/Map";
import {uniq,flatten} from "lodash";
import SectionCard from "../../components/SectionCard";
import SectionForm from "../../components/SectionForm";
// const Map = lazy(() => import("../../components/Map"));

const filterName = 'sectionData'
const emptysearch ={};
const emptyqueries =[];
const MapPage = () => {
    const intl = useIntl()
    const { openFilter, getList, getFilter, setSearch } = useFilter()

    const { queries = emptyqueries, search = emptysearch } = getFilter(filterName)
    const { value: searchValue = '' } = search

    const [fields,setFields] = useState([]);
    const [source,setSource] = useState([]);
    const [allCounties,setAllCounties] = useState([]);
    const [allDistrics,setAllDistrics] = useState([]);
    const [list,setList] = useState([]);
    const [selectedSection,setSelectedSection] = useState({})

    const [isPending,startTransition] = useTransition();
    useEffect(()=>{
        getApp().getSections().then(({data})=>{
            return getApp().getSectionFields().then(({data:fields})=>{
                setFields(fields.map(f=>({name:f.name,label:f.name})));
                // clean data
                data.forEach(d=>{
                    if (d['GPS (End)'] !== null && d['GPS (End)'] !== '-')
                        d['_GPSEnd'] = dmstoLongLat(d['GPS (End)']);
                    if (d['GPS (Start)'] !== null && d['GPS (Start)'] !== '-')
                        d['_GPSStart'] = dmstoLongLat(d['GPS (Start)']);
                    if (d['County'] !== null && d['County'] !== '-')
                        d['_County'] = seperateStr(d['County'])
                });
                setSource(data, fields);
            })
        }).catch(()=>{
            setFields([]);
            setSource([]);
        })
    },[])
    const listRef = useRef(null)
    const viewboxRef = useRef(null)

    // useEffect(() => {
    //     if (listRef.current) {
    //         listRef.current.scrollToItem(0, 'center')
    //     }
    // }, [listRef])

    useEffect(()=>{
        startTransition(()=>{
            const list = getList(filterName, source, fields);
            setList(list);
            setAllCounties(uniq(flatten(list.filter(d=>d["_County"]!==null).map(d=>d["_County"]))));
            setAllDistrics(uniq(flatten(list.filter(d=>d["District"]!==null).map(d=>d["District"]))));
        });
    },[queries,search,source, fields])
    const Row = (item,i) => {
        return <SectionCard data={item} id={i}
        highlighted={selectedSection.Section===item.Section}
                            onSelected={()=>setSelectedSection(item)}
        />
    }
    const onCloseDetail = useCallback(()=>{
        setSelectedSection({});
    },[selectedSection])

    const onSetSeclectedSection = useCallback((Section)=>{
        const selected = list.find(d=>d.Section===Section);
        if (selected)
            setSelectedSection(selected);
    },[list])
    return (
        <Page
            pageTitle={intl.formatMessage(
                {
                    id: 'map',
                    defaultMessage: 'Section information',
                },
                { count: list.length }
            )}
            contentStyle={{ overflow: 'hidden' }}
            appBarContent={
                <Toolbar disableGutters>
                    <SearchField
                        initialValue={searchValue}
                        onChange={(v) => {
                            setSearch(filterName, v)
                        }}
                    />
                    <IconButton color="inherit" onClick={() => openFilter(filterName)}>
                        <FilterList color={queries.length > 0 ? 'secondary' : undefined} />
                    </IconButton>
                </Toolbar>
            }
        >
            <Grid container style={{ height: '100%' }}>
                <Grid item xs={5} md={4} sx={{zIndex:1}}>
                    <Paper elevation={16} sx={{ height: '100%', width: '100%', p:1 }} ref={viewboxRef}>
                        {selectedSection.Section&& <Scrollbars><SectionForm Section={selectedSection?selectedSection.Section:undefined} onClose={onCloseDetail}/></Scrollbars>}
                        <Scrollbars style={{display:selectedSection.Section?'none':undefined}}>
                            <ViewportList
                                ref={listRef}
                                viewportRef={viewboxRef}
                                items={list}
                            >
                                {Row}
                            </ViewportList>
                        </Scrollbars>
                    </Paper>
                </Grid>
                <Grid item xs={7} md={8} style={{height: '100%'}}>
                    <Map allCounties={allCounties} plotCountyOption={true}
                         allDistrics={allDistrics} plotDistrictOption={true}
                         roadData={list}
                         onClick={onSetSeclectedSection}
                    ></Map>
                </Grid>
            </Grid>

            <FilterDrawer fields={fields} name={filterName} />
        </Page>
    )
}

export default MapPage

const COL_LAT = 'lat',
    COL_LONG = 'lng';

function dmstoLongLat(string){
    const dicarr = string.match(/[A-Z]/gi);
    let temp = {};
    if(dicarr !==null) {
        const numstrarr = string.split(/[A-Z]/i)
        temp[COL_LONG] =  convertorGPS(str2num(numstrarr[2]));
        temp[COL_LAT] = convertorGPS(str2num(numstrarr[1]));
        if (dicarr[0] === "S"||dicarr[1] === "s") {
            temp[COL_LAT] = -temp[COL_LAT];
        }
        if (dicarr[1] === "W"||dicarr[1] === "w") {
            temp[COL_LONG] = -temp[COL_LONG];
        }
    }else{
        const numstrarr = string.split(',');
        temp[COL_LONG] =  +numstrarr[1];
        temp[COL_LAT] = +numstrarr[0];
    }
    return temp;
    function str2num(str){
        return str.split(/ï¿½|'|"|Â°/).map(d=>+d);
    }
    function convertorGPS([d,min,sec]){
        return d + (min/60) + (sec/3600);
    }
}

function LongLattodms(arr){
    try {
        let str = '';
        let tempNum = arr[COL_LAT];
        if (tempNum < 0) {
            str += 'S';
            tempNum = -tempNum;
        } else
            str += 'N';
        str += num2str(tempNum) + ', ';
        tempNum = arr[COL_LONG];
        if (tempNum < 0) {
            str += 'W';
            tempNum = -tempNum;
        } else
            str += 'E';
        str += num2str(tempNum);

        return str;
    }catch(e){
        return arr;
    }
    function num2str (num){
        let d = Math.ceil(num);
        num = (num-d)*60;
        let m = Math.ceil(num);
        let sec = (num-m)*60;
        return d+'o'+m+"'"+'"';
    }
}

function seperateStr(str){
    return str.split(/&|\/| and /).map(d=>d.trim().toLowerCase());
}