import {useEffect, useState} from "react";
import UploadWraper from "./UploadSegment";
import {getApp} from "../../containers/Database/app";
import {useDatabase} from "../../Providers/Database";
import {group} from "d3";

function FileCollection({Section,SurveyLevels,SurveyTypes,list}){
    const {getList} = useDatabase();
    const [fileByType,setFileByType] = useState(new Map())
    const onEditFile = (file,{SurveyLevel,SurveyType})=>{
        return getApp().editSurveyData(Section,file)
    }

    useEffect(()=>{
        setFileByType(group(list??[],d=>d["Survey Type"]));
    },[list])

    return (<>
        {getList('surveyType').map((t)=><UploadWraper levels={getList('surveyLevel')}
                                                      Section={Section}
                                                      filelist={fileByType.get(t['Short Name'])}
                                                           category={t['Short Name']}
            title={t.Name}
            key={t.Name}
            onEditFile={onEditFile}/>)}
        </>)
}
export default FileCollection