import React, {useCallback, useEffect, useReducer} from 'react'
import Context from './Context'
import {_components} from "../../containers/Database/internal";
import {uniq} from 'lodash';
import {getApp} from "../../containers/Database/app";

function reducer(state, action) {
    const { type, path, isLoading=false,error = false,
        hasError = false, value } = action;
    switch (type) {
        case "LOADING_CHANGED":
            return { ...state, [path]: { ...state[path], isLoading } };
        case "VALUE_CHANGE":
            return {
                ...state,
                [path]: { ...state[path], value, isLoading, error, hasError },
            };
        case "INIT":
            return {...state,isInit:value}
        default:
            throw new Error()
    }
}

const init = {
    loading:false,
    error:false,
    isInit:false,
    surveyType:{},
    surveyLevel:{},
    section:{}
}

const Provider = ({  children, name }) => {
    const [state, dispatch] = useReducer(reducer, init);
    const _app = getApp(name);

    useEffect(()=>{
        if (!state['surveyType']) {
            try {
                dispatch({type: 'LOADING_CHANGED', path: 'surveyType', isLoading: true});
                _app.getSurveyType().then(({data}) => {
                    dispatch({type: 'VALUE_CHANGE', path: 'surveyType', value: data, isLoading: false,});
                }).catch(error => {
                    dispatch({
                        type: "ERROR",
                        path: 'surveyType',
                        isLoading: false,
                        error,
                        hasError: true,
                    });
                })
                dispatch({type: 'LOADING_CHANGED', path: 'surveyLevel', isLoading: true});
                _app.getSurveyLevel().then(({data}) => {
                    dispatch({type: 'VALUE_CHANGE', path: 'surveyLevel', value: data, isLoading: false,});
                }).catch(error => {
                    dispatch({
                        type: "ERROR",
                        path: 'surveyLevel',
                        isLoading: false,
                        error,
                        hasError: true,
                    });
                })
            } catch (e) {

            }
        }
    },[])

    const isLoading = useCallback(
        (path) => {
            return state[path] ? state[path].isLoading : false;
        },
        [state]
    );
    const getList = useCallback(
        (path) => {
            return state[path] && state[path].value ? state[path].value : [];
        },
        [state]
    );
    const querySection = useCallback(
        (Section) => {
            dispatch({type: 'LOADING_CHANGED', path: 'section', isLoading: true});
            _app.getSection(Section)
                .then(({data})=> {
                    dispatch({type: 'VALUE_CHANGE', path: 'section', value: data, isLoading: false,});
                })
                .catch(error=>{
                    dispatch({
                        type: "ERROR",
                        path: 'section',
                        isLoading: false,
                        error,
                        hasError: true,
                    });
                })
        },
        [state]
    );
    const getSection = useCallback(
        (Section) => {
            return state.section && state.section.value && Section===state.section.value.Section ? state.section.value : {};
        },
        [state]
    );
    return (
        <Context.Provider value={{
            isLoading,
            getList,
            querySection,
            getSection
        }}>
            {children}
        </Context.Provider>
    )
}

export default Provider;