import * as React from 'react';
import * as yup from 'yup';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Grid from "@mui/material/Unstable_Grid2";
import {useEffect, useState} from "react";
import {Fields, counties, districts, getApp} from "../../containers/Database/app";
import {
    Autocomplete,
    Button,
    Dialog,
    DialogActions,
    DialogTitle,
    IconButton,
    Stack,
    Toolbar,
    Typography
} from "@mui/material";
import {Edit as EditIcon, Close as CloseIcon, LocationOn} from '@mui/icons-material';
import FileCollection from "../FileCollection";
import {useDatabase} from "../../Providers/Database";
import {useFormik} from "formik";

yup.addMethod(yup.string, "gpsFormat", function (errorMessage) {
    return this.test(`test-gps-format`, errorMessage, function (value) {
        const { path, createError } = this;
        const checkMark = value?(value==='-'?[0,0]:value.match(/(N|W|n|w)[-+]?([0-9]*\.[0-9]+|[0-9]+)Â°( |)[-+]?([0-9]*\.[0-9]+|[0-9]+)'( |)[-+]?([0-9]*\.[0-9]+|[0-9]+)"/g)):null;
        return (
            (checkMark&&checkMark.length===2) ||
            createError({ path, message: errorMessage })
        );
    });
});

const validationSchema = yup.object({
    Section: yup
        .string('Enter your password')
        .min(3, 'Section should be of minimum 3 characters length')
        .required('Section is required'),
    'GPS (Start)': yup.string().gpsFormat('Wrong GPS format'),
    'GPS (End)': yup.string().gpsFormat('Wrong GPS format'),
});

const fieldMeta = {
    'Section':{xs:6},
    'CSJ': {xs:6},
    'Highway': {xs:6},
    'County': {xs:6},
    'District': {xs:6},
    'State': {xs:6},
    'Direction': {xs:6},
    'Data Type': {},
    'Reference Marker': {},
    'GPS (Start)': {},
    'GPS (End)': {},
    'Construction Year': {},
    'Horizontal Alignment': {},
    'Vertical Alignment': {},
    'Pavement Type': {},
    'Slab Thickness': {},
    'Basetype & Thickness': {},
    'Subgrade Treat & Thick': {},
    'Shoulder Type': {},
    'Surface Texture': {},
    'Concrete CAT': {},
    'Drainage': {},
}
const emptyArray=[];
const emptyObject={};
const emptyFunction=()=>{};
function RenderField ({accessorKey,xs,type,options=emptyArray,InputLabelProps,...props}) {

    return <Grid item xs={xs} key={accessorKey}>
        {(type==='choice')?
            <Autocomplete
                fullWidth
                size="small"
                options={options??[]}
                {...props}
                renderInput={(params) => <TextField label={props.label} {...params} />}
            />:(type==='gps')?
                <TextField
                    fullWidth
                    type={type}
                    size="small"
                    InputLabelProps={{
                        shrink: true,
                        ...InputLabelProps
                    }}
                    {...props}/>
            :<TextField
                fullWidth
                type={type}
                size="small"
                InputLabelProps={{
                    shrink: true,
                }}
            {...props}/>}
    </Grid>
}

export default function SectionForm({Section,onClose=emptyFunction}) {
    const [isLoading,setIsLoading] = useState(true);
    const [openGPSFormat,setOpenGPSFormat] = useState();
    // const [value,setValue] = useState({});
    const {getSection,querySection,isLoading: checkLoading} = useDatabase();
    const formik = useFormik({
        initialValues: {
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            debugger
            alert(JSON.stringify(values, null, 2));
        },
    });

    useEffect(()=>{
        querySection(Section);
    },[Section]);

    useEffect(()=>{
        setIsLoading(checkLoading('section'));
    },[checkLoading('section')]);

    useEffect(()=>{
        // setValue(getSection(Section));
        // value["SurveyData"]
        const val = (getSection(Section))
        if (val.Section)
            formik.setValues(getSection(Section))
    },[getSection(Section)])
    return (
        <div key={`form_${Section}`}>
            <Box
                component="form"
                // noValidate
                onSubmit={formik.handleSubmit}
                // autoComplete="off"
            >
                <Toolbar sx={{p:0}}>
                    <Button
                        edge="start"
                        aria-label="edit"
                        size="small"
                        variant="contained"
                        startIcon={<EditIcon />}
                        type="submit"
                    >
                        Edit
                    </Button >
                    <Button
                        edge="start"
                        aria-label="edit"
                        size="small"
                        variant="outlined"
                        startIcon={<LocationOn />}
                    >
                        Location
                    </Button >
                    <Typography sx={{ flexGrow: 1 }}></Typography>
                    <IconButton
                        edge="start"
                        aria-label="edit"
                        size="small"
                        onClick={onClose}
                    >
                        <CloseIcon />
                    </IconButton>
                </Toolbar>
                <Grid container spacing={1}>
                    {Fields.map(({accessorKey,header,type,options})=>
                        <RenderField key={accessorKey}
                                     label={header}
                                     name={header}
                                     value={formik.values[accessorKey]?(formik.values[accessorKey]==='-'?null:formik.values[accessorKey]):null}
                                     xs={fieldMeta[accessorKey].xs??6}
                                     onChange={formik.handleChange}
                                     error={Boolean(formik.errors[accessorKey])}
                                     helperText={formik.errors[accessorKey]}
                                     accessorKey={accessorKey}
                                     options={options}
                                     type={type}
                        />)}
                    <Grid item xs={12}>
                        <FileCollection Section={formik.values['Section']} list={formik.values["SurveyData"]}/>
                    </Grid>
                </Grid>
            </Box>
            {openGPSFormat&&<GPSDialog
                selectedValue={formik.values[openGPSFormat.key]}
                open={true}
                onClose={(val) => formik.setValue(openGPSFormat.key,val)}
            />}
        </div>
    );
}

const validationSchemaGPS = yup.object({
    Na: yup.number(),
    Nb: yup.number(),
    Nc: yup.number(),
    Wa: yup.number(),
    Wb: yup.number(),
    Wc: yup.number(),
});

function GPSDialog(props) {
    const { onClose, selectedValue, open, value } = props;
    const formik = useFormik({
        initialValues: {
            Na:0,
            Nb:0,
            Nc:0,
            Wa:0,
            Wb:0,
            Wc:0,
        },
        validationSchema: validationSchemaGPS,
        onSubmit: (values) => {
            debugger
            alert(JSON.stringify(values, null, 2));
        },
    });
    useEffect(()=>{
        if (value) {
            const dicarr = value.match(/[A-Z]/gi);
            if (dicarr !== null) {
                const numstrarr = value.split(/[A-Z]/i)
                const lat = str2num(numstrarr[1]);
                const long = str2num(numstrarr[2]);
                if (dicarr[0] === "S" || dicarr[1] === "s") {
                    lat[0] = -lat[0];
                }
                if (dicarr[1] === "E" || dicarr[1] === "e") {
                    long[0] = -long[0];
                }
                formik.setValues({
                    Na: lat[0],
                    Nb: lat[1],
                    Nc: lat[2],
                    Wa: long[0],
                    Wb: long[1],
                    Wc: long[2],
                })
            }

            function str2num(str) {
                return str.split(/ï¿½|'|"|Â°/).map(d => +d);
            }
        }
    },[value])

    const handleClose = () => {
        onClose(value);
    };
    const handleSave = () => {
        const {Na,Nb,Nc,Wa,Wb,Wc} = formik.values;
        onClose(`N${Na}Â°${Nb}' ${Nc}" W${Wa}Â°${Wb}' ${Wc}"`);
    };
    return (
        <Dialog onClose={handleClose} open={open}>
            <DialogTitle>GPS format</DialogTitle>
            <Stack direction="row" spacing={1} sx={{marginLeft:5,marginRight:5}}>
                <Typography>N</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Na"}
                    value={formik.values.Na}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography>Â°</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Nb"}
                    value={formik.values.Nb}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography>'</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Nc"}
                    value={formik.values.Nc}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography style={{marginRight:10 }}>" </Typography>
                <Typography>W</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Wa"}
                    value={formik.values.Wa}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography>Â°</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Wb"}
                    value={formik.values.Wb}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography>'</Typography>
                <TextField
                    variant="standard"
                    hiddenLabel
                    size="small"
                    type={"number"}
                    name={"Wc"}
                    value={formik.values.Wc}
                    onChange={formik.handleChange}
                    sx={{maxWidth:50}}
                />
                <Typography>"</Typography>
            </Stack>
            <DialogActions>
                <Button onClick={handleClose}>Cancel</Button>
                <Button onClick={handleSave}>Save</Button>
            </DialogActions>
        </Dialog>
    );
}