import {Chip, Collapse, Divider, ListItem, ListItemText, Typography} from "@mui/material";
import React from 'react'


const CustomTag = ({category,label,...props})=>{
    return <Chip
        label={<><Typography component="span"
                             variant="caption"
                             color="textSecondary">
            {category}
        </Typography>
            <Typography component="span"
                        style={{marginLeft:5}}
                        variant="body2">
                | {label}
            </Typography>
        </>}
        variant="outlined"
        {...props}
    />
}

const Fields = [
    {accessorKey: 'CSJ', header: 'CSJ'},
    {accessorKey: 'Highway', header: 'Highway'},
    {accessorKey: 'County', header: 'County'},
    {accessorKey: 'District', header: 'District'},
    {accessorKey: 'State', header: 'State'},
    {accessorKey: 'Direction', header: 'Direction'},
    {accessorKey: 'Data Type', header: 'Data Type'},
    {accessorKey: 'Reference Marker', header: 'Reference Marker'},
    {accessorKey: 'GPS (Start)', header: 'GPS (Start)'},
    {accessorKey: 'GPS (End)', header: 'GPS (End)'},
    {accessorKey: 'Construction Year', header: 'Construction Year'},
    {accessorKey: 'Horizontal Alignment', header: 'Horizontal Alignment'},
    {accessorKey: 'Vertical Alignment', header: 'Vertical Alignment'},
    {accessorKey: 'Pavement Type', header: 'Pavement Type'},
    {accessorKey: 'Slab Thickness', header: 'Slab Thickness (in.)'},
    {accessorKey: 'Basetype & Thickness', header: 'Basetype & Thickness (in.)'},
    {accessorKey: 'Subgrade Treat & Thick', header: 'Subgrade Treat & Thick'},
    {accessorKey: 'Shoulder Type', header: 'Shoulder Type'},
    {accessorKey: 'Surface Texture', header: 'Surface Texture'},
    {accessorKey: 'Concrete CAT', header: 'Concrete CAT'},
    {accessorKey: 'Drainage', header: 'Drainage'},
]

const SectionCard = ({data,id,highlighted,onSelected})=>{
    const { Section, County, District, Highway } = data;

    return <div key={`${Section}_${id}`}>
        <ListItem alignItems="flex-start" component={"div"} >
            <ListItemText
                primary={Section}
                secondary={
                    <React.Fragment>
                        <CustomTag
                            category={'Highway'}
                            label={Highway}
                            size="small"
                            color="info"
                        />
                        {(County&&(County!=='-'))&&<CustomTag
                            category={'County'}
                            label={County}
                            size="small"
                        />}
                        {(District&&(District!=='-'))&&<CustomTag
                            category={'District'}
                            label={District}
                            size="small"
                        />}
                    </React.Fragment>
                }
                onClick={onSelected}
            />
        </ListItem>
        <Divider />
    </div>
}

export default SectionCard